<template>
  <div class="test-container">
    <el-upload
      class="upload-demo"
      drag
      action=""
      ref="upload"
      accept=".xlsx,.xls"
      :before-upload="handleMembersUpload"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text"><em></em></div>
      <div class="el-upload__tip" slot="tip">
        xlsx/xls
      </div>
    </el-upload>

    <el-upload
      class="upload-demo"
      drag
      action=""
      ref="upload"
      accept=".xlsx,.xls"
      :before-upload="handleStrengthsUpload"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text"><em></em></div>
      <div class="el-upload__tip" slot="tip">
        xlsx/xls
      </div>
    </el-upload>

    <el-upload
      class="upload-demo"
      drag
      action=""
      ref="upload"
      accept=".xlsx,.xls"
      :before-upload="handleClassUpload"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text"><em></em></div>
      <div class="el-upload__tip" slot="tip">
        xlsx/xls
      </div>
    </el-upload>
  </div>
</template>

<script>
import XLSX from 'xlsx';
import { FIELDS_4, ADVANTAGES_34 } from '@/utils/strengths';

export default {
  name: 'TestView',
  data() {
    return {};
  },
  methods: {
    findAdvantage(cname) {
      // eslint-disable-next-line operator-linebreak
      const advantage =
        ADVANTAGES_34.find((item) => item.CName === cname) || {};
      // eslint-disable-next-line operator-linebreak
      const field =
        FIELDS_4.find((item) => item.fieldEName === advantage.Field) || {};

      return {
        ...advantage,
        ...field,
      };
    },
    handleMembersUpload(file) {
      console.log(file);
      const reader = new FileReader();
      reader.onload = (e) => {
        const data = e.target.result;
        const workbook = XLSX.read(data, { type: 'binary' });
        console.log(workbook, 'workbook');
        // eslint-disable-next-line operator-linebreak
        const sheetName =
          workbook.SheetNames[workbook.SheetNames.indexOf('10')];
        console.log(sheetName, 'sheetName');
        const sheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(sheet);
        console.log(jsonData);
        const members = [];
        jsonData.forEach((item) => {
          // console.log(item);
          if (item['1']) {
            members.push({
              name: item[''],
              class: item[''],
              advantage1: this.findAdvantage(item['1']),
              advantage2: this.findAdvantage(item['2']),
              advantage3: this.findAdvantage(item['3']),
              advantage4: this.findAdvantage(item['4']),
              advantage5: this.findAdvantage(item['5']),
              advantage6: this.findAdvantage(item['6']),
              advantage7: this.findAdvantage(item['7']),
              advantage8: this.findAdvantage(item['8']),
              advantage9: this.findAdvantage(item['9']),
              advantage10: this.findAdvantage(item['10']),
            });
          }
        });
        console.log(members);
        localStorage.setItem('members', JSON.stringify(members));
      };
      reader.readAsBinaryString(file);
    },
    handleStrengthsUpload(file) {
      console.log(file);
      const reader = new FileReader();
      reader.onload = (e) => {
        const data = e.target.result;
        const workbook = XLSX.read(data, { type: 'binary' });
        // console.log(workbook, 'workbook');
        const sheetName = workbook.SheetNames[0];
        // console.log(sheetName, 'sheetName');
        const sheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(sheet);
        // console.log(jsonData);
        const advantages = [];
        jsonData.forEach((item) => {
          // console.log(item);
          if (item['']) {
            // 
            // 
            // 
            // 
            // 
            // 
            // 
            // 
            // 
            // /
            // 
            // 

            const [CName, EName] = item[''].split('\n');
            // console.log(CName, EName);
            const contrast = [];
            const contrastStr = item[''];
            // console.log(contrastStr);

            const contrastArr = contrastStr.split('\n');
            // console.log(
            //   '%c [ contrastArr ]-131',
            //   'font-size:13px; background:pink; color:#bf2c9f;',
            //   contrastArr
            // );
            contrastArr.forEach((str) => {
              if (str) {
                const [cname, desc] = str.split('');
                // console.log(cname, 'xxx', desc.trim(), 'xxx');
                const temp = this.findAdvantage(cname);
                contrast.push({
                  name: cname,
                  color: temp.color,
                  desc: desc.trim(),
                });
              }
            });

            // console.log(
            //   '%c [ contrast ]-149',
            //   'font-size:13px; background:pink; color:#bf2c9f;',
            //   contrast
            // );

            advantages.push({
              CName,
              EName,
              detailInfo: item[''],
              coreReading: item[''],
              status: item[''],
              behavior: item[''],
              contribution: item[''],
              requirement: item[''],
              value: item[''],
              value2: item[''],
              metaphor: item['/'],
              obstacle: item[''],
              contrastStr: item[''],
              contrast,
              img: '',
              video: '',
              tags: [],
            });
          }
        });
        // console.log(JSON.stringify(advantages));
        localStorage.setItem('advantages', JSON.stringify(advantages));
      };
      reader.readAsBinaryString(file);
    },
    handleClassUpload(file) {
      console.log(file);
      const reader = new FileReader();
      reader.onload = (e) => {
        const data = e.target.result;
        const workbook = XLSX.read(data, { type: 'binary' });
        // console.log(workbook, 'workbook');
        const sheetName = workbook.SheetNames[0];
        console.log(sheetName, 'sheetName');
        const sheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(sheet);
        console.log(jsonData, 'jsonData');

        const classmates = [];
        jsonData.forEach((item) => {
          // console.log(item);
          if (item['']) {
            const advantages = {};
            // 6-10: ", , , , "
            // eslint-disable-next-line operator-linebreak
            const advantages6to10Arr =
              item['6-10'].split(',');
            // eslint-disable-next-line operator-linebreak
            const advantages1to5Arr =
              item['5'].split(',');
            const advantages30to34 = item['']
              ? item[''].split(' ')
              : [];
            advantages6to10Arr.forEach((o, idx) => {
              advantages[`advantage${idx + 6}`] = this.findAdvantage(
                // eslint-disable-next-line comma-dangle
                o.trim(),
              );
            });
            advantages1to5Arr.forEach((o, idx) => {
              advantages[`advantage${idx + 1}`] = this.findAdvantage(
                // eslint-disable-next-line comma-dangle
                o.trim(),
              );
            });
            advantages30to34.forEach((o, idx) => {
              advantages[`advantage${idx + 30}`] = this.findAdvantage(
                // eslint-disable-next-line comma-dangle
                o.trim(),
              );
            });
            console.log(advantages, 'advantages');
            // 6-10: ", , , , "
            // : ""
            // : ""
            // : "nancytam55"
            // : ""
            // : ", , , , "
            // : ""
            // : ""
            classmates.push({
              name: `${item['']}(${item['']})`,
              class: 8, // item[''],
              ...advantages,
              industry: item[''],
              position: item[''],
              // wxNickname: item[''],
              // problem: item[''],
              // want: item[''],
            });
          }
        });
        console.log(JSON.stringify(classmates));
        localStorage.setItem('classmates', JSON.stringify(classmates));
      };
      reader.readAsBinaryString(file);
    },
  },
};
</script>

